<template>
<div class="batch-control-container">
  <miner-list class="mb" @addIps="addSelectedIps"></miner-list>
  <el-row type="flex" class="common-box mb" style="padding: 20px;">
    <ip-range ref="ipRange"></ip-range>
    <div v-loading="loading" element-loading-text="">
      <div style="padding-left: 40px;">
        <el-button type="primary" @click="reboot"></el-button>
        <el-button v-if="false" type="primary" @click="shutdown"></el-button>
      </div>
    </div>
  </el-row>
</div>
</template>

<script>
import IpRange from '@/components/IpRange'
import MinerList from '@/components/MinerList'

export default {
  name: 'BatchControl',
  components: {
    IpRange,
    MinerList
  },
  data () {
    return {
      loading: false
    }
  },
  computed: {
    checkedIps () {
      return this.$refs.ipRange.checkedIps
    }
  },
  methods: {
    addSelectedIps (ipList) {
      this.$refs.ipRange.addSelectedIps(ipList)
    },
    reboot () {
      if (this.checkedIps.length === 0) {
        this.$alert(' IP ', '')
        return
      }
      this.$confirm('ip ', '').then(() => {
        console.log('reboot')
        this.loading = true
        this.$ajax.put('/v1/batch/reboot', {
          ips: this.checkedIps
        })
          .then((response) => {
            this.loading = false
            if (response.data.result === 'success') {
              this.$alert('')
            } else {
              this.$alert('')
            }
          })
          .catch((error) => {
            this.loading = false
            this.$alert(error)
          })
      }).catch(() => {
        // 
      })
    },
    shutdown () {
      if (this.checkedIps.length === 0) {
        this.$alert(' IP ', '')
        return
      }
      this.$confirm('ip ', '').then(() => {
        console.log('shutdown')
        this.loading = true
        this.$ajax.put('/v1/batch/shutdown', {
          ips: this.checkedIps
        })
          .then((response) => {
            this.loading = false
            if (response.data.result === 'success') {
              this.$alert('')
            } else {
              this.$alert('')
            }
          })
          .catch((error) => {
            this.loading = false
            this.$alert(error)
          })
      }).catch(() => {
        // 
      })
    }
  }
}
</script>

<style>

</style>



